import React from 'react'
import { Link } from 'gatsby'
import 'animate.css/animate.min.css'
import ScrollAnimation from 'react-animate-on-scroll'
import styled from 'styled-components'
import theme from '@style/theme'

class Navi extends React.Component {
  render() {
    const { location, title } = this.props
    return (
      <Nav>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          offset={0}
          duration={0.3}
        >
          <Link to="/">
            <Brand>{title}</Brand>
          </Link>
        </ScrollAnimation>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          offset={0}
          duration={0.3}
        >
          
          {location.pathname === '/about/' ? (
            <Link to="/about/">
              <NavLink><Focus>About</Focus></NavLink>
            </Link>
          ):(
            <Link to="/about/">
              <NavLink><Normal>About</Normal></NavLink>
            </Link>
          )}
        </ScrollAnimation>
      </Nav>
    )
  }
}

export default Navi

const Nav = styled.div`
  position: fixed;
  font-family: ${theme.font.primary};
  width: 100%;
  padding: 5px 0;
  
  @media (max-width: ${theme.screen.md}) {
    display: grid;
    grid-template-columns: 100px 1fr;
    align-content: center;
  }
`
const Brand = styled.div`
  padding: 10px 20px;
  font-weight: bold;
  ${theme.fontSize.lg};
  @media (max-width: ${theme.screen.md}) {
    padding: 10px 15px;
  }
`
const Normal = styled.span`
  :before {
    content: '';
    padding-left: 0;
    overflow: hidden;
    transition: 0.2s padding;
  }
  :hover{
    color: ${theme.color.grayscale.S950};
    :before{
      padding: 0 3px;
    }
  }
`
const Focus = styled.span`
  color: ${theme.color.primary.S600D};
  :before {
    content: '⁃';
    margin-right: 5px;
  }
}
`
const NavLink = styled.div`
  text-decoration: none;
  float: left;
  display: inline-block;
  text-align: center;
  padding: 0px 20px;
  font-weight: bold;
  ${theme.fontSize.sm};
  color: ${theme.color.grayscale.S950};
  @media (max-width: ${theme.screen.md}) {
    padding: 10px 15px;
  }
`